*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

table{
  width: 100%;
}

table th,
table td{
  border-bottom: 1px solid #ccc;
  padding: 4px 8px;
  text-align: left;
}

button{
  padding: 2px 4px;
  font-size: 12px;
  margin-right: 2px;
}

.data-form form{
  display: flex;
  flex-wrap: wrap;
  background: #eee;
  align-items: flex-end;
  padding: 10px;
  margin-top:20px;
}
.data-form form >div{
  width: 50%;
  padding: 4px 10px;
}
.data-form label{
  font-size: 10px;
  text-transform: uppercase;
  font-weight: bold;
}
.data-form input,
.data-form label{
  display: block;
  width: 100%;
}
